<template>
    <button
            class="hw-button"
            :class="'hw-button-size-'+size"
            @click="handleClick"
    >
        <slot></slot>
    </button>
</template>

<script>
    export default {
        name: "hw-button",
        props: {
            size: {
                type: String,
                default: 'big'
            }
        },
        methods: {
            handleClick() {
                this.$emit('click')
            }
        }
    }
</script>

<style scoped>
    .hw-button {
        display: inline-block;
        line-height: 1;
        cursor: pointer;
        background: #fff;
        border: 1px solid #ddd;
        color: #f00;
        text-align: center;
        outline: none;
        font-size: 14px;
        border-radius: 4px
    }

    .hw-button.hw-button-size-big {
        padding: 12px 15px
    }

    .hw-button.hw-button-size-mid {
        padding: 9px 15px
    }

    .hw-button.hw-button-size-small {
        padding: 7px 15px
    }

    .hw-button.hw-button-size-mini {
        padding: 5px 10px
    }
</style>
